"use client"

import React from "react"
import { usePathname } from "next/navigation"
import { AppShell } from "@/components/app-shell"

type BreadcrumbItem = { label: string; href?: string }

function pretty(seg: string) {
  // mapeie rotas amigáveis
  const map: Record<string, string> = {
    dashboard: "Dashboard",
    controls: "Controles",
    execucao: "Registrar Execução",
    revisao: "Fila de Revisão",
    "planos-acao": "Planos de Ação",
    admin: "Admin",
  }
  return map[seg] ?? seg.charAt(0).toUpperCase() + seg.slice(1)
}

function buildBreadcrumbs(pathname: string): BreadcrumbItem[] {
  // pathname ex: /controls ou /execucao?...
  const path = pathname.split("?")[0]
  const segments = path.split("/").filter(Boolean)

  // Para rotas base, breadcrumb simples
  if (segments.length === 0) return []

  const crumbs: BreadcrumbItem[] = []
  let acc = ""
  for (let i = 0; i < segments.length; i++) {
    const seg = segments[i]
    acc += `/${seg}`

    // Último segmento sem href (padrão breadcrumb)
    if (i === segments.length - 1) crumbs.push({ label: pretty(seg) })
    else crumbs.push({ label: pretty(seg), href: acc })
  }
  return crumbs
}

export default function AppLayout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname()
  const breadcrumbs = buildBreadcrumbs(pathname || "/")

  return <AppShell breadcrumbs={breadcrumbs}>{children}</AppShell>
}
